<template>
	<div class="ComponentRoot App" ref="AgendatorApp">
	
		<div class="AgendatorEditor">
			<textarea-autosize
				id="agendator-input"
				class="AgendatorEditorTextarea"
				placeholder="ayuda"
				ref="commandInput"
				v-model="command"
				:minHeight="0"
				:maxHeight="350"
				:width="'100%'"
				:onCtrlEnterKeyPress="executeCommand.bind(this)"
			/>
		</div>
	
		<div class="CommandWhisperer">
			<span></span> {{ lastCommand.command }}<span v-if="lastCommand.title"> ({{lastCommand.title}})</span>
		</div>
		<div class="Viewer">




			<div class="Tarjeta" v-if="selected.view === 'historico'">
				<div class="Titulo">Ver histórico</div>
				<ul class="BulletList">
					<li v-for="command in commandHistoryReversed">
						<span>{{ command.command }}</span>
						<span v-if="command.title">: {{ command.title }}</span>
						<ul>
							<li v-for="(attribute, index) in command.attributes" v-if="command.attributes">
								<span class="Label">{{attribute.key}}:</span>
								<span> {{attribute.value}}</span>
							</li>
							<li v-if="command.contents">
								<span class="Label">Contenido:</span> 
								<span> {{ command.contents }}</span>
							</li>
						</ul>
					</li>
				</ul>
			</div>





			<div class="Tarjeta" v-else-if="selected.view === 'ver tarea'">
				<div class="Titulo">Ver tarea</div>
				<div v-if="typeof selected.task === 'object'">
					<span class="Label">Ver tarea: </span> 
					<span v-if="selected.task.title">{{ selected.task.title }}</span>
					<span v-else>-</span>
					<ul>
						<li v-if="selected.task.contents">
							<span class="Label">Título:</span> 
							<span>{{ selected.task.title ? selected.task.title : "-" }}</span>
						</li>
						<li v-for="(attribute, index) in selected.task.attributes" v-if="selected.task.attributes">
							<span class="Label">{{attribute.key}}: </span>
							<span>{{attribute.value}}</span>
						</li>
						<li v-if="selected.task.contents">
							<span class="Label">Contenido:</span> 
							<span>{{ selected.task.contents }}</span>
						</li>
					</ul>
				</div>
				<div v-else>
					<span class="Label">Ups:</span> no se encontró ninguna tarea como <b>"{{"" + lastCommand.title}}"</b>.
				</div>
			</div>





			<div class="Tarjeta" v-else-if="selected.view === 'ver tareas'">
				<div class="Titulo">Ver tareas</div>
				<div class="SubtarjetaList" v-if="(typeof commandState.tasks === 'object') && (Object.keys(commandState.tasks).length !== 0)">
					<div class="Subtarjeta" v-for="(task, taskIndex) in commandState.tasks">
						<span>
							<span class="Label" v-if="task.title">{{task.title}}</span>
						</span>
						<ul>
							<li v-for="(attribute, index) in computeAttributes(task.attributes)" v-if="task.attributes">
								<span class="Label">{{index}}:</span>
								<span> {{attribute}}</span>
							</li>
							<li v-if="task.contents">
								<span class="Label">Contenido:</span>
								<span> {{task.contents}}</span>
							</li>
						</ul>
					</div>
				</div>
				<div v-else>
					<span class="Label">Ups:</span> no se encontró ninguna tarea.
				</div>
			</div>





			<div class="Tarjeta" v-else-if="selected.view === 'ver tareas por urgencia'">
				<div class="Titulo">Ver tareas por urgencia</div>
				<ul>
					<li v-for="command in commandHistory">
						<div><span class="Label">Comando:</span> {{ command.command }}</div>
						<div v-if="command.title"><span class="Label">Título:</span> {{ command.title }}</div>
						<ul>
							<li v-for="(attribute, index) in command.attributes" v-if="command.attributes">
								<span class="Label">{{attribute.key}}: </span>
								<span>{{attribute.value}}</span>
							</li>
						</ul>
						<div v-if="command.contents"><span class="Label">Contenido:</span> {{ command.contents }}</div>
					</li>
				</ul>
			</div>





			<div class="Tarjeta" v-else-if="selected.view === 'ver tareas por fecha'">
				<div class="Titulo">Ver tareas por fecha</div>
				<ul>
					<li v-for="(date, taskGroup) in tasksByDate">
						<div class="Label">{{date}}</div>
						<ul>
							<li v-for="(task, taskIndex) in taskGroup">
								<span>{{task}}</span>
							</li>
						</ul>
					</li>
				</ul>
			</div>





			<div class="Tarjeta" v-else-if="selected.view === 'hecho'">
				<div class="Titulo">Ver tareas hechas</div>
				<ul v-if="(typeof commandState.dones === 'object') && (Object.keys(commandState.dones).length !== 0)">
					<li v-for="(hecho, hechoIndex) in commandState.dones" :key="'hechos-' + hechoIndex">
						<div><span class="Label">Comando:</span> {{ hecho.command }}</div>
						<div v-if="hecho.title"><span class="Label">Título:</span> {{ hecho.title }}</div>
						<ul>
							<li v-for="(attribute, index) in hecho.attributes" v-if="hecho.attributes">
								<span class="Label">{{attribute.key}}: </span>
								<span>{{attribute.value}}</span>
							</li>
						</ul>
						<div v-if="hecho.contents"><span class="Label">Contenido:</span> {{ hecho.contents }}</div>
					</li>
				</ul>
			</div>

			



			<div v-else>
				<div class="Tarjeta">
					<div class="Titulo">Ver ayuda</div>
					<ul class="CommandsList" v-if="commandKnowledgeList && commandKnowledgeList.length">
						<li class="CommandItem" v-for="(methodName, methodIndex) in commandKnowledgeList" :key="'help-entries-' + methodIndex">
							<span>{{ methodName }}</span>
						</li>
					</ul>
					<div v-else>No se han encontrado comandos.</div>
				</div>
				<div class="Tarjeta">
					<div class="Titulo">Pautas</div>
					<ul>
						<li>
							<span>Escribir el comando con el formato:</span>
							<pre class="Code">{comando}: {título o parámetro principal}
- {atributo 1}: {valor 1}
- {atributo 2}: {valor 2}
- {atributo 3}: {valor 3}
{Contenido del comando.}</pre>
						</li>
						<li>
							<span>Ejecutar comando presionando las teclas 
								<pre class="InlineBlock">[Control]</pre>
							 + 
							 <pre class="InlineBlock">[Enter]</pre>.</span>
						</li>
						<li>
							<span>En móviles, presionando el botón de <b>play</b> de la esquina inferior derecha.</span>
						</li>
					</ul>
				</div>
			</div>






		</div>
		<div class="ErrorBox" v-if="commandError">
			{{ commandError }}
		</div>
		<button class="ExecuteButton" v-on:click="executeCommand">▶</button>
	</div>
</template>
<script>

import Agendator from "./agendator.parser.js";
import comandos from "./commands/comandos.js";

const loadCommandHistory = () => {
	if("APP_AGENDATOR_STORE" in localStorage) {
		return JSON.parse(localStorage.APP_AGENDATOR_STORE);
	} else {
		localStorage.APP_AGENDATOR_STORE = "[]";
		return [];
	}
};

const saveCommandHistory = (commands) => {
	localStorage.APP_AGENDATOR_STORE = JSON.stringify(commands);
};

const normalizeDate = (dateFormatted) => {
	const tokens = dateFormatted.match(/([0-9][0-9][0-9][0-9])[^0-9]([0-1]?[0-9])[^0-9]([0-3]?[0-9])[^0-9]([0-2]?[0-9])[^0-9]([0-5]?[0-9])/g);
	if(!tokens) {
		return null;
	}
	let [match, year, month, date, hour, minute] = tokens;
	if(month.length === 1) {
		month = "0" + month;
	}
	if(date.length === 1) {
		date = "0" + date;
	}
	if(hour.length === 1) {
		hour = "0" + hour;
	}
	if(minute.length === 1) {
		minute = "0" + minute;
	}
	return `${year}/${month}/${date} ${hour}:${minute}`;
};

const normalizeRepetition = (repetition) => {

};

export default {
	// name: "",
	components: {},
	data() {
		const DEFAULT_COMMAND_STATE = {
			tasks: {},
			dones: [],
			/*
			contacts: {},
			alarms: [],
			notes: [],
			//*/
		};
		return {
			DEFAULT_COMMAND_STATE: DEFAULT_COMMAND_STATE,
			command: "",
			lastCommand: {command:"ayuda"},
			commandHistory: loadCommandHistory(),
			commandHistoryIndex: 0,
			commandFontSize: 15,
			commandPadding: 0,
			commandError: null,
			commandState: Object.assign({}, DEFAULT_COMMAND_STATE),
			// tasksByUrgency: [],
			// tasksByDate: [],
			commandKnowledge: {
				ayuda(command) {
					
				},
				historico() {
					
				},
				...comandos
			},
			selected: {
				view: "ayuda",
				task: null
			}
		};
	},
	methods: {
		executeCommand() {
			const command = this.parseCommand(this.command);
			this.lastCommand = command;
			this.command = "";
			// @TOIMPROVE: make it storagefull, statefull, not storageless/stateless... :(
			this.commandError = null;
			try {
				this.execute([].concat(this.commandHistory).concat(this.lastCommand));
			} catch(error) {
				this.commandError = error;
				return;
			}
			this.commandHistory.push(command);
			this.commandHistoryIndex = (this.commandHistory && this.commandHistory.length) ? this.commandHistory.length-1 : 0;
			this.saveCommandHistory(this.commandHistory);
		},
		parseCommand(text) {
			const command = {};
			try {
				const data = Agendator.parse(text);
				this.commandError = null;
				return data;
			} catch(error) {
				this.commandError = error;
				throw error;
			}
		},
		resetState() {
			this.commandState = Object.assign({}, this.DEFAULT_COMMAND_STATE);;
			console.log("state:", this.commandState);
		},
		updateState(commands) {
			this.resetState();
			commands.forEach((command, index) => {
				//if(!confirm(JSON.stringify(command))) throw new Error()
				console.log("state:", this.commandState);
				this.executeSingleCommand(command, index, commands, this);
			});
		},
		execute(commands) {
			this.updateState(commands);
		},
		executeSingleCommand(command, index, commands) {
			console.log("commands:", this.commandKnowledge, command);
			if(command.command in this.commandKnowledge) {
				this.commandError = null;
				const commandKnowledge = this.commandKnowledge[command.command];
				commandKnowledge(command, index, commands, this);
			} else {
				this.commandError = new Error("Comando no encontrado:" + command.command);
				throw this.commandError;
			}
		},
		checkCommandState() {
			this.parseCommand(this.command);
		},
		saveCommandHistory(commandHistory = this.commandHistory) {
			return saveCommandHistory(commandHistory);
		},
		setCommandHistory(commandHistory) {
			if(commandHistory) {
				this.commandHistory = commandHistory;
				this.saveCommandHistory(this.commandHistory);
			}
		},
		isPersistentCommand(cmd) {
			if([
				"borrar último comando",
				"crear tarea",
				"añadir tarea",
				"insertar tarea",
				"editar tarea",
				"modificar tarea",
				"cambiar tarea",
				"eliminar tarea",
				"suprimir tarea",
				"borrar tarea",
				"quitar tarea",
				"crear contacto",
				"añadir contacto",
				"insertar contacto",
				"editar contacto",
				"modificar contacto",
				"cambiar contacto",
				"eliminar contacto",
				"suprimir contacto",
				"borrar contacto",
				"quitar contacto",
				].indexOf(cmd) !== -1) {
				return true;
			}
		},
		computeAttributes(attrs) {
			if(Array.isArray(attrs)) {
				return attrs.reduce((result, item) => {
					result[item.key] = item.value;
					return result;
				}, {});
			} else {
				return {};
			}
		}
	},
	watch: {
		commandKnowledge(newValue) {
			this.commandKnowledgeList = Object.keys(newValue ? newValue : {});
		}
	},
	computed: {
		commandLines() {
			return this.command.split(/\n/g).length;
		},
		commandKnowledgeList() {
			return Object.keys(this.commandKnowledge ? this.commandKnowledge : {}).sort();
		},
		commandHistoryReversed() {
			return this.commandHistory.reverse();
		},
		tasksByUrgency() {
			// @TODO:
			const normalTasks = Object.keys(this.commandState.tasks).reduce((result, item) => {
				// @TODO: add normal tasks.
				return result;
			}, {});
			const allTasks = ((out) => {
				// @TODO: add repeated tasks from the first date until the last date, or 1 week after and 1 week before by default.
				return out;
			})(normalTasks);
			return allTasks;
		},
		tasksByDate() {
			// @TODO:
			const repeatedTasks = [];
			const addTaskByDate = (result, task) => {
				if("fecha" in task) {
					const fecha = task.fecha;
					const fechaNormalizada = normalizeDate(fecha);
					if(!(fechaNormalizada in result)) {
						result[fechaNormalizada] = [];
					}
					result[fechaNormalizada].push(task);
				} else if(("desde" in task) || ("hasta" in task)) {
					const desde = task.desde;
					const hasta = task.hasta;
					const desdeNormalizado = normalizeDate(fecha);
					const hastaNormalizado = normalizeDate(fecha);
					if(!(desdeNormalizado in result)) {
						result[desdeNormalizado] = [];
					}
					result[desdeNormalizado].push(task);
					if(!(hastaNormalizado in result)) {
						result[hastaNormalizado] = [];
					}
					result[hastaNormalizado].push(task);
				}
				if("repetir") {
					repeatedTasks.push(task);
				}
			};
			const normalTasks = Object.keys(this.commandState.tasks).reduce((result, taskName) => {
				// @TODO: add normal tasks.
				const task = this.commandState.tasks[taskName];
				addTaskByDate(result, task);
				return result;
			}, {});
			const allTasks = ((out) => {
				// @TODO: add repeated tasks from the first date until the last date, or 1 week after and 1 week before by default.
				return out;
			})(normalTasks);
			return allTasks;
		}
	},
	mounted() {
		document.querySelector("html").addEventListener("click", () => {
			document.querySelector("#agendator-input").focus();
		});
		window.AGENDATOR = this;
	},
};

</script>
<style>

@font-face {
	font-family: "Roboto";
	src: url("./assets/fonts/Roboto-Regular.ttf");
}

html, body {
	margin: 0;
	padding: 0;
	font-family: Roboto;
	background-color: #333;
	color: #AAA;
	text-shadow: 0px 0px 1px black;
}
h1,h2,h3,h4,h5,h6,ul,li,hr {
	padding: 0;
	margin: 0;
}
ul {
	padding-left: 1px;
}
ul.BulletList {
	margin-left: -13px;
}
ul.BulletList li {
	padding-left: 13px !important;
}

li {
	list-style-type: bullet;
	list-style-position: inside;
}
hr {
	height: 0px;
	border: 0px solid #333;
	border-bottom: 1px solid #333;
}

input, textarea {
	display: block;
}


@media (max-width: 600px) {
  html, body {
    font-size: 10px;
  }
}
@media (min-width: 600px) {
  html, body {
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  html, body {
    font-size: 18px;
  }
}

.App {
}
.App .CommandInput {
	padding: 3px;
	width: 100%;
	height: auto;
	resize: none;
	box-sizing: border-box;
	font-family: monospace;
}
.App .CommandWhisperer {
	padding: 3px;
	box-sizing: border-box;
	text-align: right;
    background-color: #333;
    color: #666;
	float: right;
	margin-top: 1px;
	margin-bottom: 1px;
	text-transform: uppercase;
}
.App .Viewer {
	padding: 0px;
	box-sizing: border-box;
}
.App .ExecuteButton {
	position: fixed;
	top: auto;
	left: auto;
	bottom: 7px;
	right: 6px;
	box-sizing: border-box;
	border-radius: 3pt;
	border: 0px solid #333;
	background-color: white;
	width: 23px;
	height: 23px;
	cursor: pointer;
	color: white;
	background-color: #333;
	transition: color .2s linear;
}
.App .ExecuteButton:hover {
	color: #0f0;
}
.App .AgendatorEditor {
	width: 100%;
	box-sizing: border-box;
	margin: 0;
	padding-right: 0px;
}
.App .AgendatorEditorTextarea {
	width: 100%;
	box-sizing: border-box;
	background-color: black;
	border: 1px solid black;
	color: #0f0;
}
.App .ErrorBox {
	position: fixed;
	top: auto;
	left: 0px;
	bottom: 0px;
	right: 0px;
	box-sizing: border-box;
	background-color: red;
	color: yellow;
	padding: 10px;
	opacity: 0.8;
}
.App .Label {
	display: inline-block;
	border: 1px solid #888;
	margin-top: 1px;
	margin-left: 0px;
	border-radius: 2pt;
	padding: 1px;
	color: #777;
}
.App .NoSpaces {
	margin: 0;
	padding: 0;
}
.App .AgendatorEditorTextarea:focus {
	outline: none;
}

.App ul {
	display: block;
}
.App .InlineBlock {
	display: inline-block;
	margin: 0;
}

.App .Tarjeta {
	background-color: #333;
	color: #FFF;
	padding: 1px;
	margin-bottom: 1px;
}
.App .Tarjeta > .Titulo {
	display: block;
	background-color: black;
	padding: 3px;
	text-transform: uppercase;
}

.App .Tarjeta .Label {
	background-color: #333;
}

.App .Subtarjeta {
	background-color: #555;
	color: #FFF;
	padding: 3px;
	margin-bottom: 1px;
}

.App .SubtarjetaList {
	margin-top: 1px;
}

.App .Code {
	margin: 0;
	padding: 5px;
	background-color: #BBB;
	color: #000;
	border-radius: 1pt;
	padding-top: 1px;
	padding-bottom: 1px;
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;
}

.App .CommandsList {
	text-align: justify;
	margin-bottom: 1px;
	margin-right: 1px;
}
.App .CommandItem {
	display: inline-block;
	border: 1px solid #888;
	margin-top: 1px;
	margin-left: 1px;
	border-radius: 2pt;
	padding: 3px;
	color: #777;
}

</style>