const newCommands = {};
newCommands["crear tarea"] = (command, index, commands, scope) => {
	let wasFound = false;
	if(!(command.title in scope.commandState.tasks)) {
		scope.commandState.tasks[command.title] = Object.assign({}, command);
		wasFound = false;
	} else {
		wasFound = true;
	}
	if((index+1) === commands.length) {
		if(wasFound) {
			scope.commandError = new Error("Tarea ya creada: " + command.title);
			throw scope.commandError;
		}
		scope.selected.task = scope.commandState.tasks[command.title];
		scope.selected.view = "ver tarea";
	}
};
newCommands["añadir tarea"] = newCommands["crear tarea"];
newCommands["registrar tarea"] = newCommands["crear tarea"];
newCommands["insertar tarea"] = newCommands["crear tarea"];
newCommands["nueva tarea"] = newCommands["crear tarea"];
newCommands["editar tarea"] = (command, index, commands, scope) => {
	console.log("Editando tarea: ", command);
	const wasFound = (command && command.title && (command.title in scope.commandState.tasks));
	if(wasFound) {
		Object.keys(command).forEach(key => {
			if((key !== "attributes") && (command[key] !== null)) {
				scope.commandState.tasks[command.title][key] = command[key];
			}
		});
		const newAttributes = [];
		(scope.commandState.tasks[command.title].attributes || []).forEach(attr => {
			scope.commandState.tasks[command.title].attributes.push(attr);
		});
		command.attributes.forEach(attribute => {
			(command.attributes || []).forEach(attr => {
				newAttributes.push(attr);
			});
		});
		scope.commandState.tasks[command.title].attributes = newAttributes;
	}
	if((index+1) === commands.length) {
		if(!wasFound) {
			scope.commandError = new Error("Tarea no creada: " + command.title);
			throw scope.commandError;
		} else {
		}
		scope.selected.task = scope.commandState.tasks[command.title];
		scope.selected.view = "ver tarea";
	}
};
newCommands["modificar tarea"] = newCommands["editar tarea"];
newCommands["cambiar tarea"] = newCommands["editar tarea"];
newCommands["alterar tarea"] = newCommands["editar tarea"];
newCommands["borrar tarea"] = (command, index, commands, scope) => {
	const isTaskDefined = command.title in scope.commandState.tasks;
	if(isTaskDefined) {
		delete scope.commandState.tasks[command.title];
	}
	if((index+1) === commands.length) {
		scope.selected.view = "ver tareas";
	}
};
